.link-underline {
  position: relative;
  display: inline-block;
}
.link-underline::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: width 0.3s ease;
}
.link-underline:hover::after {
  width: 100%;
}
.button-glow {
  position: relative;
  overflow: hidden;
}
.button-glow::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at center, rgba(11, 148, 68, 0.3), transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.button-glow:hover::before {
  opacity: 1;
}
:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
